import '../scss/styles.scss';

const LIFETIME_SECOND_SLIDE = 3800;
const LIFETIME_THIRD_SLIDE = 3100;

const TIME_TO_SHOW_SECOND_SLIDE = 2500;
const TIME_TO_HIDE_FIRST_SLIDE = 200;
const TIME_TO_SHOW_THIRD_SLIDE = TIME_TO_SHOW_SECOND_SLIDE + LIFETIME_SECOND_SLIDE;
const TIME_TO_SHOW_FOURTH_SLIDE = TIME_TO_SHOW_THIRD_SLIDE + LIFETIME_THIRD_SLIDE;
const TIME_TO_SHOW_FIFTH_SLIDE = TIME_TO_SHOW_THIRD_SLIDE + LIFETIME_THIRD_SLIDE * 2;

const firstSlide = document.getElementById('firstSlide');
const secondSlide = document.getElementById('secondSlide');
const thirdSlide = document.getElementById('thirdSlide');
const fourthSlide = document.getElementById('fourthSlide');
const fifthSlide = document.getElementById('fifthSlide');

setTimeout(() => {
    secondSlide.style.display = 'block';
}, TIME_TO_SHOW_SECOND_SLIDE);

setTimeout(() => {
    firstSlide.style.display = 'none';
}, TIME_TO_SHOW_SECOND_SLIDE + TIME_TO_HIDE_FIRST_SLIDE);

setTimeout(() => {
    secondSlide.style.display = 'none';
    thirdSlide.style.display = 'block';
}, TIME_TO_SHOW_THIRD_SLIDE);

setTimeout(() => {
    thirdSlide.style.display = 'none';
    fourthSlide.style.display = 'block';
}, TIME_TO_SHOW_FOURTH_SLIDE);

setTimeout(() => {
    fourthSlide.style.display = 'none';
    fifthSlide.style.display = 'block';
}, TIME_TO_SHOW_FIFTH_SLIDE);
